.crappo__showcase {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    gap: 3rem;
}

.crappo__showcase-illustration {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.crappo__showcase-illustration img {
    width: 150%;
    height: 100%;
}

.crappo__showcase-value {
    display: flex;
    flex-direction: column;
}

.crappo__showcase-value_banner {
    display: flex;
}

.crappo__showcase-value_banner p {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 32px;
    font-family: var(--font-rubik);
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: var(--color-white);
    display: flex;
    align-items: center;
    padding-right: 1rem;
}

.crappo__showcase-value_banner p span {
    background: var(--color-white);
    border-radius: 32px;
    font-family: var(--font-rubik);
    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: var(--color-black);
    padding: 4px 16px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-left: 5px;
}

.crappo__showcase-value_text h1 {
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 64px;
    line-height: 78px;
    color: var(--color-white);
}

.crappo__showcase-value_text p {
    font-family: var(--font-rubik);
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: #E0E0E0;
    margin-top: 1rem;
}

.crappo__showcase-value_btn button {
    margin-top: 1rem;
    font-family: var(--font-rubik);
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    color: var(--color-white);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 16px 16px 16px 24px;
    background: #3671E9;
    border-radius: 32px;
    border: 0;
    width: 202px;
    height: 64px;
    outline: none;
}

.rotate-vert-center {
    -webkit-animation: rotate-vert-center 7s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
    animation: rotate-vert-center 7s cubic-bezier(0.455, 0.030, 0.515, 0.955) infinite both;
}

@-webkit-keyframes rotate-vert-center {
    0% {
        -webkit-transform: rotateY(0);
        transform: rotateY(0);
    }

    100% {
        -webkit-transform: rotateY(360deg);
        transform: rotateY(360deg);
    }
}

@keyframes rotate-vert-center {
    0% {
        -webkit-transform: rotateY(0);
        transform: rotateY(0);
    }

    100% {
        -webkit-transform: rotateY(360deg);
        transform: rotateY(360deg);
    }
}

@media screen and (max-width: 990px) {
    .crappo__showcase {
        flex-direction: column-reverse;
    }

    .crappo__showcase-value_banner p {
        font-size: 14px;
        line-height: 20px;
    }

    .crappo__showcase-value_banner p span {
        font-size: 14px;
        line-height: 20px;
    }

    .crappo__showcase-value_text h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .crappo__showcase-value_text p {
        font-size: 14px;
        line-height: 20px;
    }

    .crappo__showcase-value_btn button {
        font-size: 14px;
        line-height: 150%;
    }

    .crappo__showcase-illustration {
        width: 60%;
        height: 60%;
        margin: auto;
    }
}


@media screen and (max-width: 550px) {


    .crappo__showcase-value_banner p {
        font-size: 10px;
        line-height: 16px;
    }

    .crappo__showcase-value_banner p span {
        font-size: 10px;
        line-height: 16px;
    }

    .crappo__showcase-value_text h1 {
        font-size: 28px;
        line-height: 150%;
    }

    .crappo__showcase-value_text p {
        font-size: 10px;
        line-height: 16px;
    }

    .crappo__showcase-value_btn button {
        font-size: 10px;
        line-height: 16px;
        padding: 16px 16px 16px 24px;
        border-radius: 32px;
        width: 170px;
        height: 54px;
    }
}