.crappo__ctacomp {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 2rem 1rem;
    margin-bottom: 3rem;
}

.crappo__ctacomp:hover {
    background: #2B076E;
    backdrop-filter: blur(125px);
    border-radius: 16px;
    color: var(--color-white);
}

.crappo__ctacomp:hover h3 {
    color: var(--color-white);
}

.crappo__ctacomp:hover p {
    color: var(--color-white);
}

.crappo__ctacomp-img img {
    width: 80px;
    height: 80px;
}

.crappo__ctacomp h3 {
    margin-top: 3rem;
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 32px;
    line-height: 48px;
    color: var(--color-black);
}

.crappo__ctacomp h3 span {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    opacity: 0.7;
    vertical-align: super;
    margin-left: 0.5rem;
}

.crappo__ctacomp p {
    margin-top: 2rem;
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.01em;
    color: var(--color-gray);

}

.crappo__ctacomp-icon {
    margin-top: 3rem;

    display: flex;
    justify-content: center;
    align-items: center;
}


.crappo__ctacomp-icon img {
    border: 2px solid rgba(43, 7, 110, 0.2);
    padding: 1.0rem 1.5rem;
    border-radius: 50%;
}

.crappo__ctacomp-btn_extra {
    margin-top: 4rem;
}

.crappo__ctacomp-btn_extra button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 16px 16px 24px;
    gap: 24px;
    background: var(--color-blue);
    border-radius: 32px;
    border: 0;
    outline: 0;
    color: var(--color-white);
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
}