.crappo__footer .grid {
    display: grid;
    grid-template-columns: auto auto auto auto;
}

.crappo__footer .grid .section-one {
    display: flex;
    justify-content: space-between;
    gap: 20rem;
    flex-direction: column;
}

.crappo__footer .grid .section-one footer {
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: var(--color-white);
}

.crappo__footer .grid .section-two {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0 50px;
}

.crappo__footer .grid .section-two h3 {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    font-family: var(--font-rubik);
    color: var(--color-white);

}

.crappo__footer .grid .section-two li {
    list-style: none;
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 16px;
    line-height: 38px;
    color: #F2F2F2;
}

.crappo__footer .grid .section-three {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0 30px 0 0;
}

.crappo__footer .grid .section-three h3 {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    font-family: var(--font-rubik);
    color: var(--color-white);

}

.crappo__footer .grid .section-three li {
    list-style: none;
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 16px;
    line-height: 38px;
    color: #F2F2F2;
}

.crappo__footer .grid .section-four {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.crappo__footer .grid .section-four p {
    font-weight: 500;
    font-size: 32px;
    line-height: 48px;
    font-family: var(--font-rubik);
    color: var(--color-white);
    max-width: 25rem;
}

.crappo__footer .grid .section-four .payment-methods {
    display: flex;
    gap: 2rem;
}

.crappo__footer .grid .section-four .socials {
    display: flex;
    gap: 1rem;
}

.crappo__footer .responsive {
    display: none;
    flex-direction: column;
    color: var(--color-white);
    gap: 3rem;
}

.crappo__footer .responsive .one {

    display: flex;
    justify-content: space-between;
    gap: 1.5rem;
}

.crappo__footer .responsive .one .section-two {
    display: flex;
    flex-direction: column;
}

.crappo__footer .responsive .one .section-two h3 {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    font-family: var(--font-rubik);
    color: var(--color-white);

}

.crappo__footer .responsive .one .section-two li {
    list-style: none;
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
}

.crappo__footer .responsive .one .section-three {
    display: flex;
    flex-direction: column;
}

.crappo__footer .responsive .one .section-three h3 {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    font-family: var(--font-rubik);
    color: var(--color-white);

}

.crappo__footer .responsive .one .section-three li {
    list-style: none;
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
}

.crappo__footer .responsive .two {
    display: flex;
}

.crappo__footer .responsive .two .section-four {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.crappo__footer .responsive .two .section-four p {
    font-weight: 500;
    font-size: 26px;
    line-height: 150%;
    font-family: var(--font-rubik);
    color: var(--color-white);
    max-width: 25rem;
}

.crappo__footer .responsive .two .section-four .payment-methods {
    display: flex;
    gap: 2rem;
}

.crappo__footer .responsive .section-four .socials {
    display: flex;
    gap: 1rem;
}

.crappo__footer .responsive .two .section-five {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.crappo__footer .responsive .section-five footer {
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: var(--color-white);

}

@media screen and (max-width: 1200px) {
    .crappo__footer .grid .section-two h3 {
        font-size: 16px;
        line-height: 150%;
    }

    .crappo__footer .grid .section-two li {
        font-size: 12px;
        line-height: 150%;
    }

    .crappo__footer .grid .section-three h3 {
        font-size: 16px;
        line-height: 150%;
    }

    .crappo__footer .grid .section-three li {
        font-size: 12px;
        line-height: 150%;
    }

    .crappo__footer .grid .section-four p {
        font-size: 24px;
        line-height: 150%
    }

    .crappo__footer .grid .section-four {
        justify-content: space-evenly;
    }


}

@media screen and (max-width: 900px) {

    .crappo__footer .grid {
        display: none !important;
    }

    .crappo__footer .responsive {
        display: flex;
    }

    .crappo__footer .responsive .two {
        flex-direction: column;
    }

    footer {
        margin-top: 3rem;

    }

}