.crappo__stats {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.crappo__stats-all_group {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.crappo__stats-graph {
    display: flex;
    font-family: var(--font-rubik);
}

.crappo__stats-graph-icon {
    width: 80px;
    height: 80px;
}

.crappo__stats-graph-icon img {
    width: 80px;
    height: 80px;
}

.crappo__stats-graph-numtext {
    margin: 0 20px;
}

.crappo__stats-graph-numtext h2 {
    font-weight: 600;
    font-size: 40px;
    line-height: 60px;
    color: var(--color-white);
}

.crappo__stats-graph-numtext p {
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: #E0E0E0;
}

@media screen and (max-width: 990px) {
    .crappo__stats-graph-numtext h2 {
        font-weight: 500;
        font-size: 20px;
        line-height: 40px;
        color: var(--color-white);
        margin-top: 10px;

    }

    .crappo__stats-graph-numtext p {
        font-weight: 600;
        font-size: 10px;
        line-height: 18px;
        letter-spacing: 0.01em;
        color: #E0E0E0;
        margin-top: -5px;
    }


}

@media screen and (max-width: 650px) {
    .crappo__stats-all_group {
        flex-direction: column;
        gap: 30px;
        align-items: center;
    }

    .crappo__stats-graph-numtext h2 {
        font-weight: 500;
        font-size: 40px;
        line-height: 60px;
        color: var(--color-white);
        margin-top: 3px;

    }

    .crappo__stats-graph-numtext p {
        font-weight: 16px;
        line-height: 24px;
        letter-spacing: 0.01em;
        color: #E0E0E0;
    }
}