.crappo__navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 2rem;
}

.crappo__navbar-logo {
    width: 135px;
    height: 40px;
    margin-top: 0.3rem;
}

.crappo__navbar-links {
    display: flex;
    align-items: center;
    color: var(--color-white);
    justify-content: center;
    font-family: var(--font-rubik);

}

.crappo__navbar-links p {
    padding-right: 2rem;
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
}

.crappo__navbar-links p.login {
    font-weight: 400;
}

.crappo__navbar-links img {
    margin: 0 1.5rem 0 0;
}

.crappo__navbar-links button {
    background: #3671E9;
    border-radius: 32px;
    padding: 14px 32px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    border: 0;
    outline: none;
    color: var(--color-white);
}

.crappo__navbar-menu {
    display: none;
    margin-top: 1rem;
}

.crappo__navbar-menu .crappo__navbar-menu-container {
    background-color: var(--color-blue);
    padding: 2rem;
    width: 10rem;
    height: 250px;
}

.crappo__navbar-menu_container-links p {
    color: var(--color-white);
    font-family: var(--font-rubik);
    padding-bottom: 1rem;
    text-align: right;
    cursor: pointer;
}



@media screen and (max-width:990px) {
    .crappo__navbar-menu {
        display: block;

    }

    .crappo__navbar-links {
        display: none;
    }


}