.crappo__cta {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fbf9fd;
}

.crappo__cta .crappo__cta-estimated {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    background: var(--color-white);
    border-radius: 0px 0px 16px 16px;
}

.crappo__cta .crappo__cta-estimated p:first-child {
    font-family: var(--font-rubik);
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.04em;
    color: var(--color-blue);
}

.crappo__cta .crappo__cta-estimated p:nth-child(2) {
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 32px;
    line-height: 48px;
    letter-spacing: 0.04em;
    color: #0D0D2B;
}

.crappo__cta .crappo__cta-estimated p:nth-child(2) span {
    color: var(--color-blue);

}

.crappo__cta .crappo__cta-estimated p:nth-child(3) {
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: var(--color-gray);
}

.crappo__cta-txt {
    display: flex;
    align-items: center;
    justify-content: center;
}

.crappo__cta-txt h2 {
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 40px;
    line-height: 150%;
    text-align: center;
    color: #0D0D2B;
}

.crappo__cta-banners {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    background-color: var(--color-white);
}

.crappo__cta-banner {
    border-radius: 16px;
    margin: 1rem;
}

@media screen and (max-width: 950px) {
    .crappo__cta .crappo__cta-estimated {
        flex-direction: column;
        padding: 20px;
    }

    .crappo__cta-txt h2 {
        font-size: 30px;
        line-height: 150%;
    }

}

@media screen and (max-width: 990px) {
    .crappo__cta-banners {
        flex-direction: column;
    }
}