.crappo__features {
    display: flex;
    justify-content: space-between !important;
    margin-bottom: 4rem;
    gap: 3rem;
}

.crappo__features:nth-child(2) {
    flex-direction: row-reverse;
}

.crappo__features:nth-child(4) {
    flex-direction: row-reverse;
}

.crappo__features-image {
    max-width: 650px;
    max-height: 473px;
}

.crappo__features-image img {
    width: 100%;
    height: 100%;
}


.crappo__features-txt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 15px;
}

.crappo__features-txt h2 {
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    color: var(--color-white);
    text-align: left;
}

.crappo__features-txt p {
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    text-align: left;
    color: #E0E0E0;

}

.crappo__features-txt button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 24px;
    gap: 24px;
    width: 146px;
    height: 59px;
    background: #3671E9;
    border-radius: 32px;
    border: 0;
    outline: none;
    color: var(--color-white);
    font-weight: 500;
    font-size: 18px;
    font-family: var(--font-rubik);
    margin-top: 1rem;
}


@media screen and (max-width: 990px) {
    .crappo__features {
        flex-direction: column !important;
        align-items: center;
    }

    .crappo__features-txt {
        margin-top: 50px;
    }
}

@media screen and (max-width: 550px) {
    .crappo__features-txt h2 {
        font-size: 25px;
        line-height: 150%;
    }

    .crappo__features-txt p {
        font-size: 12px;
        line-height: 150%;
    }
}