.crappo__subscribe {
    background: linear-gradient(180deg, #2B076E 0%, #0D0D2B 100%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.crappo__subscribe .crappo__subscribe-banner {
    display: flex;
    background: var(--color-blue);
    border-radius: 16px;
    padding: 2rem;
    align-items: center;
    justify-content: center;
    gap: 8rem;
}

.crappo__subscribe .crappo__subscribe-banner .section-one {
    display: flex;
    flex-direction: column;
}

.crappo__subscribe .crappo__subscribe-banner .section-one h3 {
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 32px;
    line-height: 48px;
    color: var(--color-white);
}

.crappo__subscribe .crappo__subscribe-banner .section-one p {
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: var(--color-white);
}

.crappo__subscribe .crappo__subscribe-banner .section-two {
    display: flex;
    flex-direction: row;
}

.crappo__subscribe .crappo__subscribe-banner .section-two form {
    display: flex;
    flex-direction: row;
    gap: 3rem;
}

.crappo__subscribe .crappo__subscribe-banner .section-two form input {
    border: 0;
    background-color: rgba(240, 248, 255, 0);
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
    padding-right: 5rem;
}

.crappo__subscribe .crappo__subscribe-banner .section-two form input::placeholder {
    font-weight: 400;
    font-size: 16px;
    font-family: var(--font-rubik);
    line-height: 28px;
    letter-spacing: 0.01em;
    color: var(--color-white)
}

.crappo__subscribe .crappo__subscribe-banner .section-two form button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 14px 32px;
    background: var(--color-white);
    border-radius: 32px;
    border: 0;
    font-family: var(--font-rubik);
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    color: var(--first-bg);
}


@media screen and (max-width: 990px) {
    .crappo__subscribe .crappo__subscribe-banner .section-one h3 {
        font-size: 24px;
        line-height: 150%
    }

    .crappo__subscribe .crappo__subscribe-banner .section-one p {
        font-size: 12px;
        line-height: 150%;
    }
}

@media screen and (max-width: 950px) {
    .crappo__subscribe .crappo__subscribe-banner {
        flex-direction: column;
    }

    .crappo__subscribe .crappo__subscribe-banner .section-one h3 {
        text-align: center
    }

    .crappo__subscribe .crappo__subscribe-banner .section-two form {
        flex-direction: column;
    }

    .crappo__subscribe .crappo__subscribe-banner {
        gap: 2rem;
    }
}