.crappo__earnings {
    display: flex;
    flex-direction: column;
    margin-bottom: -4rem;
    justify-content: center;
    align-items: center;
}

.crappo__earnings-txt {
    display: flex;
    flex-direction: column;
}

.crappo__earnings-txt h2 {
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    color: var(--color-white);
    text-align: center;

}

.crappo__earnings-txt p {
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: #E0E0E0;
    text-align: center;

}

.crappo__earnings-calculate {
    display: flex;
    align-items: center;
    margin-top: 2rem;
    justify-content: space-between;
    background: #FBFCFE;
    border-radius: 16px 16px 0px 0px;
}

.crappo__earnings-calculate form {
    display: flex;
    flex-direction: row;
    padding: 1rem 2rem 1rem 1rem;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.crappo__earnings-calculate input {
    margin-left: 2rem;
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    border: 0;
    border-bottom: 1px solid var(--color-black);
}

.crappo__earnings-calculate select {
    margin-left: 2rem;
    font-family: var(--font-rubik);
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    border: 0;
    border-bottom: 1px solid var(--color-black);
}

.crappo__earnings-calculate button {
    margin-left: 2rem;
    font-family: var(--font-rubik);
    border: 0;
    background: var(--color-blue);
    border-radius: 32px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px 32px;
    gap: 24px;
    color: var(--color-white);
    cursor: pointer;
}

@media screen and (max-width: 950px) {
    .crappo__earnings-txt h2 {

        font-size: 30px;
        line-height: 150%;


    }

    .crappo__earnings-txt p {
        font-size: 16px;
        line-height: 150%;
    }
}


@media screen and (max-width: 650px) {
    .crappo__earnings-calculate form {
        flex-direction: column;
        /* gap: 20px; */


    }
}