.crappo__market {
    background: #2B076E;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.crappo__market .crappo__market-head h2 {
    font-family: var(--font-rubik);
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    text-align: center;
    color: var(--color-white);
    margin: 2rem 0 4rem 0;
}

@media screen and (max-width: 990px) {
    .crappo__market .crappo__market-head h2 {
        font-size: 30px;
        line-height: 150%;
    }
}